/* album.css */
.album-container {
  border: 2px solid #61dafb; /* Same color as the Upload form border */
  border-radius: 10px;
  overflow: hidden;
  margin: 20px;
  padding: 20px;
  background-color: #f0f0f0; /* Set a background color for the container */
  width: calc(100% - 40px); /* Adjust the width based on your preferences */
  max-width: 1200px; /* Set a maximum width if needed */
  height: 400px;
  margin-left: auto;
  margin-right: auto;
}

.album-header {
  height: 200px;
  text-align: center;
}

.divider {
  border-top: 3px solid #61dafb; /* Same color as the Upload form border */
  margin: 20px 0px ;
}


